$(document).ready(function() {

    // Menu
    $('.header__item-show').click(function() {
        $(this).parent('.header__item').toggleClass('header__item--active')
        if (window.matchMedia("(max-width: 576px)").matches) {
            $('.header__hidden-close').toggleClass('header__hidden-close--active')
            $('body').addClass('fixed-body')
        }
    })

    let show = $('.header__item-show')
    let item = $('.header__item')

    $(document).mouseup(function (e) {
        if ( !show.is(e.target) && show.has(e.target).length === 0 &&
            !item.is(e.target) && item.has(e.target).length === 0

        ) {
            item.removeClass('header__item--active')
        }
    })

    $('.header__hidden-close').click(function () {
        $(this).removeClass('header__hidden-close--active')
        $('.header__item').removeClass('header__item--active')
        $('body').removeClass('fixed-body')
    })

    // Burger
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header__right2').toggleClass('header__right2--active')
        $('body').toggleClass('fixed-body')
    })

    // Hover-info
    $('.users-card__amount-icon').hover(function () {
        $(this).find('.users-card__amount-hidden').toggleClass('users-card__amount-hidden--active')
    })

    //User-menu
    $('.header__user').click(function (e) {
        $(this).addClass('header__user--active')
        $('.header-user__menu').addClass('header-user__menu--active')
        $('.header-user__menu-bg').addClass('header-user__menu-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.header-user__menu-bg').click(function () {
        $(this).removeClass('header-user__menu-bg--active')
        $('.header__user').removeClass('header__user--active')
        $('.header-user__menu').removeClass('header-user__menu--active')
        $('body').removeClass('fixed-body')
    })

    //Settings-menu
    $('.settings__burger').click(function () {
        $('.settings__sidebar').addClass('settings__sidebar--active')
        $('body').addClass('fixed-body')
    })

    $('.settings__sidebar-close').click(function () {
        $('.settings__sidebar').removeClass('settings__sidebar--active')
        $('body').removeClass('fixed-body')
    })

    //Games-menu
    $('.services2__menu-btn').click(function () {
        $('.services2__left').addClass('services2__left--active')
        $('body').addClass('fixed-body')
    })

    $('.services2__left-close').click(function () {
        $('.services2__left').removeClass('services2__left--active')
        $('body').removeClass('fixed-body')
    })

    //Basket-menu
    $('.basket').click(function () {
        $('.services2-basket').addClass('services2-basket--active')
        $('body').addClass('fixed-body')
    })

    $('.services2-basket__close').click(function () {
        $('.services2-basket').removeClass('services2-basket--active')
        $('body').removeClass('fixed-body')
    })

    // Select
    $(".custom_select select").niceSelect()

    $(".form-control").select2({
        tags: true,
        tokenSeparators: [',', ' ']
    })

    //
    $('.check').click(function () {
        $('.services-step4__select2').toggleClass('services-step4__select2--active')
    })

    $('.check2').click(function () {
        $('.services-step4__select3').toggleClass('services-step4__select2--active')
    })

    $('.check3').click(function () {
        $('.services-step4__select4').toggleClass('services-step4__select2--active')
    })

    //
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').toggleClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
       var currentele = $(this).html()
       $('.select__list-default li').html(currentele)
       $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).mouseup(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })


    // Placeholder
    $('select').each(function(index,el){
        $(el).select2({
            placeholder: $(el).attr("data-placeholder")
        });
    })

    // Tabs
    $(".authorization__tab-btn").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".authorization__tab-btn").removeClass("authorization__tab-btn--active")
        $(this).addClass("authorization__tab-btn--active")

        $(this).parents(".authorization__tabs").find(".authorization__tabs-content").removeClass("authorization__tabs-content--active")
        $(this).parents(".authorization__tabs").find(`.authorization__tabs-content[data-content-path="${path}"]`).addClass("authorization__tabs-content--active")
    })

    // Tabs2
    $(".settings__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".settings__tab").removeClass("settings__tab--active")
        $(this).addClass("settings__tab--active")
        $('.settings__sidebar').removeClass('settings__sidebar--active')
        $('body').removeClass('fixed-body')

        $(this).parents(".settings__inner").find(".settings__content").removeClass("settings__content--active")
        $(this).parents(".settings__inner").find(`.settings__content[data-content-path="${path}"]`).addClass("settings__content--active")
    })

    // Tabs3
    $(".services2__game").click(function () {
        let path = $(this).attr("data-game-path")
        $(this).siblings(".services2__game").removeClass("services2__game--active")
        $(this).addClass("services2__game--active")

        $(this).parents(".services2__inner").find(".services2__right").removeClass("services2__right--active")
        $(this).parents(".services2__inner").find(`.services2__right[data-content-path="${path}"]`).addClass("services2__right--active")
    })

    // Tabs4
    $(".orders__tab").click(function () {
        let path = $(this).attr("data-order-path")
        $(this).siblings(".orders__tab").removeClass("orders__tab--active")
        $(this).addClass("orders__tab--active")

        $(this).parents(".orders__inner").find(".orders__right").removeClass("orders__right--active")
        $(this).parents(".orders__inner").find(`.orders__right[data-content-path="${path}"]`).addClass("orders__right--active")
    })

    // Tabs5
    $(".orders2__tab").click(function () {
        let path = $(this).attr("data-order-path")
        $(this).siblings(".orders2__tab").removeClass("orders2__tab--active")
        $(this).addClass("orders2__tab--active")

        $(this).parents(".orders2__inner").find(".orders2__right").removeClass("orders2__right--active")
        $(this).parents(".orders2__inner").find(`.orders2__right[data-content-path="${path}"]`).addClass("orders2__right--active")
    })

    // Recovery
    $('.authorization__form-info').click(function () {
        $('.authorization__right').css({'display':'none'})
        $('.recovery').css({'display':'block'})
    })

    $('.recovery__form-back').click(function () {
        $('.authorization__right').css({'display':'block'})
        $('.recovery').css({'display':'none'})
    })

    // Input-password
    $('body').on('click', '.password-btn', function(){
        if ($('.password').attr('type') == 'password'){
            $(this).addClass('view');
            $('.password').attr('type', 'text');
        } else {
            $(this).removeClass('view');
            $('.password').attr('type', 'password');
        }
        return false;
    });

    // Input-file
    $('#review-file').on('change', function (e) {
        span = $(this).next('label');
        span.html(e.target.files[0].name);

        newImg = $('<img />');
        var reader = new FileReader();

        reader.onload = function (r) {
            newImg.attr('src', r.target.result).attr('style', 'width: 28px; height: 28px; border-radius: 50%; order: -1; margin-right: 10px;');
        }

        reader.readAsDataURL(e.target.files[0]);
        newImg.appendTo(span);
    });

    // Input-disabled
    $(".settings__info-btn").click(function () {
        let path = $(this).attr("data-btn-path")
        $(this).toggleClass("settings__info-btn--active")

        $(this).parents(".settings__content").find(`.edit-text[data-input-path="${path}"]`).toggleClass("edit-text--active")
    })


    // Services
    $(".services__item input").click(function () {
        let path = $(this).attr("data-tab-path")

        $(this).parents(".services").find(".game").removeClass("game--active")
        $(this).parents(".services").find(`.game[data-content-path="${path}"]`).addClass("game--active")
    })


    // Textarea
    $('.textarea').bind('input', function(){
        $('.result').html($(this).val().length);
    });

    $('.textarea2').bind('input', function(){
        $('.result2').html($(this).val().length);
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    //Accordion
    $(".info__item-show").click((function(){$(this).toggleClass("info__item-show--active")
    $(this).siblings(".info__item-hidden").slideToggle()}));

    // Rating
    const ratings = document.querySelectorAll('.rating')
    if (ratings.length > 0) {
        initRatings();
    }

    function initRatings() {
        let ratingActive, ratingValue;
        for (let index = 0; index < ratings.length; index++) {
            const  rating = ratings[index];
            initRating(rating);
        }

        function initRating(rating) {
            initRatingVars(rating);
            setRatingActiveWidth();
            if (rating.classList.contains('rating__set')) {
                setRating(rating);
            }
        }
        
        function  initRatingVars(rating) {
            ratingActive = rating.querySelector('.rating__active');
            ratingValue = rating.querySelector('.rating__value');
        }

        function setRatingActiveWidth(index = ratingValue.innerHTML) {
            const ratingActiveWidth = index / 0.05;
            ratingActive.style.width = `${ratingActiveWidth}%`;
        }

        function setRating(rating) {
            const ratingItems = rating.querySelectorAll('.rating__item');
            for (let index = 0; index < ratingItems.length; index++) {
                const ratingItem = ratingItems[index];
                ratingItem.addEventListener('mouseenter', function (e) {
                    initRatingVars(rating);
                    setRatingActiveWidth(ratingItem.value)
                });
                ratingItem.addEventListener('mouseleave', function (e) {
                    setRatingActiveWidth();
                });
                ratingItem.addEventListener('click', function (e) {
                    initRatingVars(rating);
                    if (rating.dataset.ajax) {
                        setRatingValue(ratingItem.value, rating)
                    } else {
                        ratingValue.innerHTML = index + 1;
                        setRatingActiveWidth();
                    }
                })
            }
        }
    }

    // Quiz
    const prevBtns = document.querySelectorAll('.prev-quiz');
    const nextBtns = document.querySelectorAll('.next-quiz');
    const step = document.querySelectorAll('.step');

    let stepNum = 0;

    nextBtns.forEach(btn => {
        btn.addEventListener('click', () => {
            stepNum++;
            updateStep();
            updateProgressbar();
        })
    })

    prevBtns.forEach(btn => {
        btn.addEventListener('click', () => {
            stepNum--;
            updateStep();
            updateProgressbar();
        })
    })

    function updateStep() {
        step.forEach(formStep => {
            formStep.classList.contains('step--active') &&
                formStep.classList.remove('step--active');
        })
        
        step[stepNum].classList.add('step--active');
    }
})
